@import 'scss/vars.scss';

.scoresTable {
  height: 6.4375rem;
  width: 16.875rem;
  margin: 0 0.25rem;
  display: flex;
  text-align: center;
  overflow-x: scroll;
  font-weight: normal;

  > div {
    display: flex;
    flex-direction: column;
    justify-content: space-evenly;
    flex: 0 0 1.875rem;
    transition: all var(--transition-speed);
    border-bottom: 2px solid transparent;

    > span.hidden {
      visibility: hidden;
    }

    > span:first-child {
      font-weight: bold;
    }

    @media (hover: hover) {
      &:hover {
        background-color: white;
        cursor: pointer;
        border-bottom: 2px solid var(--side-color);
        box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
        font-weight: bold;
      }
    }

    &.active {
      background-color: white;
      cursor: pointer;
      border-bottom: 2px solid var(--side-color);
      box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
      font-weight: bold;
    }
  }
}
